<template>
  <div class="resume-self-description" v-if="selfDescription">
    <h4 class="resume-self-description__title text-center font-size-22 mb-3">自我描述</h4>
    <p class="resume-self-description__description font-size-16">
      {{ selfDescription.description }}</p>
  </div>
  <div class="resume-self-description" v-else>
    <ContentLoader
            :height="25"
            :width="400"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
    >
      <rect x="0" y="0" rx="3" ry="3" width="50" height="10" />
      <rect x="0" y="20" rx="3" ry="3" width="100%" height="6.4" />
    </ContentLoader>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader'
export default {
  name: 'ResumeSelfDescription',
  props: ['selfDescription'],
  components: {
    ContentLoader
  }
}
</script>

<style lang="scss" scoped>
  .resume-self-description {
    background-color: #fff;
    padding: 1rem 2rem;
  }
  .resume-self-description__description {
    //margin:0;
  }
</style>



