<template>
  <div class="resume-job-intension" v-if="jobIntension">
    <h4 class="resume-job-intension__title text-center font-size-22 mb-3">求职意向</h4>
    <ul class="resume-job-intension__content list-unstyled">
      <li>{{ jobIntension.position }}</li>
      <li>{{ jobIntension.location }}</li>
      <li>{{ jobIntension.salary }}</li>
      <li>{{ jobIntension.jobTime }}</li>
    </ul>
  </div>
  <div class="resume-job-intension" v-else>
    <ContentLoader
            :height="25"
            :width="400"
            :speed="2"
            primaryColor="#f3f3f3"
            secondaryColor="#ecebeb"
    >
      <rect x="0" y="0" rx="3" ry="3" width="50" height="10" />
      <rect x="0" y="20" rx="3" ry="3" width="100%" height="6.4" />
    </ContentLoader>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader'
export default {
  name: 'ResumeJobIntension',
  props: ['jobIntension'],
  components: {
    ContentLoader
  }
}
</script>

<style lang="scss" >
  .resume-job-intension {
    background-color: #fff;
    padding: 1rem 2rem;
  }
  .resume-job-intension__content{
    display: flex;
    flex-direction: row;
    justify-content: center;
    >li{
      padding:5px 10px;
    }
  }
</style>



